  @media screen and (max-width: 480px) {
    .i {
      flex-direction: column;
    }
  
    .i-left-wrapper {
    
      padding: 10px;
      align-items: center;
      justify-content: center;
      text-align: center;
      height: 100%;
    }
  
    .i-desc{
      display: none;
    }

    .i-img{
      width: 63%;
      height: 138%;
      top: 2px;
    }
    
  }

  .cs{
    color: crimson;
    font-weight: bold;
  }

  .ms{
    color: rgb(54, 18, 214);
    font-weight: bold;
  }

  h3{
    font-size: 20px;
  }